import multiCascader from "./components";

console.log("multinamn", multiCascader.name);
const install = (Vue) => {
    Vue.component("multiCascader", multiCascader)
};
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}
export default multiCascader;



